@use "./sass/variables" as *;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@media screen and (min-width: 768px) {
    .container {
        padding-left: 5rem;
        padding-right: 5rem;
    }    
}

.display-text {
    padding: 1rem;
    font-family: $font;
}